import React, { useState, useEffect } from "react";
import NavBar from "../components/navbar/NavBar";
import Fotter from "../components/fotter/Fotter";
import { Link } from "react-router-dom";
import LoadingScreen from "react-loading-screen";

const HomePage = () => {
  const [load, setLoad] = useState(true); //for loading bage
  const [articleInfo, setArticleInfo] = useState([
    {
      image: "",
      title: "",
      body: "",
      time: ""
    }
  ]);

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch("/api/newArticle/");
      const body = await result.json();
      setArticleInfo(body);
      //for loading bage
      setLoad(false);
    };
    fetchData();
  }, []);

  return (
    <>
      <LoadingScreen loading={load} bgColor="#191919" spinnerColor="white">
        <NavBar />
        <div class="grid-container">
          {articleInfo.map((item, key) => (
            <div className="grid-item" key={key}>
              <img className="homeImage" src={item.image} alt="" />
              <div className="overlay"></div>
              <div className="subContant">
                <Link to={`/article/${item._id}`}>
                  <h1>{item.title}</h1>
                </Link>
                <section
                  dangerouslySetInnerHTML={{
                    __html: item.body.substring(0, 300) + "....</p>"
                  }}
                />
                <p>{item.time}</p>
              </div>
            </div>
          ))}
        </div>
        <Fotter />
      </LoadingScreen>
    </>
  );
};
export default HomePage;
